@import '_reset';

// All the Sass variables must be on the top-level/main SCSS file in order to use YAML to populate the values

$primary_color: #eee;
$secondary_color: #006cbf;
$accent_color: #f7be00;

$font_family: 'Nunito', sans-serif;

$heading_1_font_size: 2.5rem;
$heading_1_font_weight: 700;
$heading_2_font_size: 2rem;
$heading_2_font_weight: 400;
$paragraph_font_size: 1.2rem;
$paragraph_font_weight: 300;

$background-1: "";
$background-2: "";
$background-3: "";


// Use partials located on the _sass folder to then populate the remainder of the stylesheet, keeping it all organised as you need

@import "_typography";

@mixin image-fit {
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
  }
}

.heading,
p {
  padding: 1rem;
  background-color: $accent_color;
  border-radius: 5px;
  width: 80%;
  margin: auto;
  margin-bottom: 3rem;
}

.image-container-1 {
  height: 300px;
  width: 50%;
  margin: auto;
  overflow: hidden;
  @include image-fit;
}

.image-container-2 {
  height: 800px;
  width: 80%;
  margin: auto;
  overflow: hidden;
  @include image-fit;
}
.image-container-3 {
  height: 600px;
  width: 80%;
  margin: auto;
  overflow: hidden;
  @include image-fit;
}
